* {
  box-sizing: border-box;
}
body {
  margin: 0;
  font-size: 1rem;
}
.conbox {
  display: flex;
  margin: 10px;
}
.leftnav {
  background-color: rgb(2, 247, 255);
  height: 110vh;
  padding: 1rem;
  flex: 30%;
}

.rightnav {
  padding: 1rem;
  width: 100%;
  background-color: rgb(202, 202, 218);
}
.proimage {
  text-align: center;
  height: 100vh;
}

.proimage .pro img {
  height: 15rem;
  border-radius: 50%;
  margin-top: 1rem;
}
.site-title {
  font-size: 2rem;
  margin-bottom: 20px;
}
.it,
.Itpro .its span {
  font-size: 1.2rem;
  color: black;
}
.its {
  font-size: 1rem;
  color: rgb(78, 70, 70);
}

.nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  gap: 1rem;
}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 10px;
}

.nav li.active {
  background-color: #555;
  color: white;
}

.nav li:hover {
  background-color: #777;
}

@media screen and (max-width: 350px) {
  body {
    font-size: 50%;
  }
  .proimage .pro img {
    height: 5rem;
  }
  .site-title {
    font-size: 1rem;
    margin-bottom: 20px;
  }
  .it,
  .Itpro .its span {
    font-size: 0.8rem;
    color: black;
  }
  .its {
    font-size: 0.6rem;
    color: rgb(78, 70, 70);
  }
}
